.dropdown-menu {
    background-color: #030010;
    border-radius: 5px;
    padding: 10px 20px;
    transition: opacity .5s ease;
    -webkit-transform: translate3d(0px, 22px, 0px);
            transform: translate3d(0px, 22px, 0px);
    margin-top: 10px;
    border: 1px solid rgba(255,255,255,.1);
}
.dropdown-item {
    display: block;
    width: 100%;
    padding: .25rem 1rem;
    clear: both;
    font-weight: 400;
    color: #fff;
    text-align: inherit;
    text-decoration: none;
    white-space: nowrap;
    background-color: transparent;
    border: 0;
}
.dropdown-item:focus, .dropdown-item:hover {
    color: #1e2125;
    background-color: transparent;
}


